a {
  color: #08c;
}

input {
  padding: 0.5rem;
  font-size: 16px;
  max-width: 100%;
  display: block;
  border-radius: 4px;
  border: 1px solid #ccc;
  margin-bottom: 1rem;
}

input:focus {
  border-color: #007eff;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(0, 126, 255, 0.1);
  outline: none;
}

input.error {
  border-color: red;
}

label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

.input-feedback {
  color: red;
  margin-top: 0.25rem;
}

button {
  margin: 20px 0;
  padding: 12px 20px;
  border-style: none;
  border-radius: 5px;
  background-color: #08c;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  font-size: 17px;
  font-weight: 500;
  color: #fff;
  cursor: pointer;
  outline: none;
  -webkit-appearance: none;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed !important;
}

button + button {
  margin-left: 0.5rem;
}

button.outline {
  background-color: #eee;
  border: 1px solid #aaa;
  color: #555;
}

.modalContent {
  display: flex;
  font-size: 16px;
  flex-direction: column;
  align-items: flex-start;
}

.closeModal {
  align-self: flex-end;
  font-size: 2.3em;
}

.container {
  max-width: 120rem;
  margin: 0 auto;
}

.grid {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media (max-width: 960px) {
  .container {
    max-width: 90vw;
  }
  .grid {
    justify-content: space-evenly;
  }
}

@media (max-width: 960px) {
  .container {
    width: 100vw;
  }
  .grid {
    justify-content: center;
  }
}
form {
  font-size: 16px;
}

.home {
  display: flex;
  align-items: center;
